import defaultAvatar from '@/assets/default/avatar.png';
import plainImgOne from '@/assets/perks/plain_1.png';
import plainImgTwo from '@/assets/perks/plain_2.png';
import plainImgThree from '@/assets/perks/warning_fill.png';
import meSideMenu from "@/components/meSideMenu";
import { computed, ref } from "vue";
import imgLogo from '@/assets/logo.png';
import { updateWindowHeight } from "@/utils/tools";
export default {
  name: "index",
  components: {
    meSideMenu
  },

  setup() {
    const i = ref(localStorage.getItem('identity'));
    const identity1 = computed(() => {
      return i.value;
    });
    return {
      identity1
    };
  },

  watch: {
    identity(newValue) {
      console.log(newValue);
    }

  },
  computed: {
    identity: {
      get() {
        return this.$store.state.identity;
      }

    }
  },

  data() {
    return {
      dividerPosition: 'center',
      defaultAvatar,
      imgLogo,
      plainImgOne,
      plainImgTwo,
      plainImgThree
    };
  },

  mounted() {
    let screenWidth = document.body.clientWidth;
    let screenWidthFloor = Math.floor(screenWidth);

    if (screenWidthFloor <= 768) {
      this.dividerPosition = 'left';
      updateWindowHeight();
    }

    window.onresize = () => {
      if (screenWidthFloor <= 768) {
        this.dividerPosition = 'left';
        updateWindowHeight();
      }
    };
  },

  unmounted() {
    updateWindowHeight();
    window.onresize = null;
  },

  methods: {
    turnMyDeals() {
      this.$router.push({
        path: '/deals/myDeals',
        query: {}
      });
    },

    turnMyEvents() {
      this.$router.push({
        path: '/events/myEvents',
        query: {}
      });
    },

    turnMyJobPosts() {
      this.$router.push({
        path: '/jobs/home',
        query: {}
      });
    }

  }
};